nav {
    position: fixed;
    top: 0;
    width: 100%;
    height: 50px;
    background: rgb(3, 156, 36);
}

.list {
    list-style-type: none;
    background: black;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}
.items {
    margin-right: 30px;
    font-size: 20px;
    text-transform: uppercase;
    color: #f1f1f1;
    cursor: pointer;
}

.btn {
    display: none;
    position: absolute;
    right: 10px;
    top: 10px;

    background: transparent;
    color:white;
    border: none;
    font-size: 18px;
}

.btn:hover{
    font-size: 25px;
}



@media screen and (max-width: 500px){

    .list {
        flex-direction: column;
        height: auto;
    }
    .items:nth-child(1){
        border-top: 1px solid rgba(255, 255, 255, 0.555);
        margin-top: 50px;
    }
    .items {
        width: 100%;
        border-top: 1px solid rgba(255, 255, 255, 0.555);
        text-align: center;
        margin-right: 0px;
        padding: 20px 0;
    }
    .btn {
        display: block;
    }

}